@charset "utf-8";
* {
  font-family: 'Spoqa Han Sans', 'Sans-serif';
  transition: color 0.3s;
  transition: background-color 0.3s;
  box-sizing: border-box;
}
html, body { 
  overflow-x: hidden;
}    
body, p, input {
  margin: 0;
  padding: 0;
  line-height: 1;
}
/* HTML5 display-role reset for older browsers */
header, footer, section { 
  display: block; 
}
a {
  text-decoration: none;
}
::selection {
  background: #333; 
  color: rgb(0, 205, 245);
}
button {
  border: none;
  font-size: 16px;
  font-size: 1rem;
  cursor: pointer;
}
/* animation */
@-webkit-keyframes start-flicker {
  0% {background-color: rgba(0, 135, 245, 0.9);}
  50% {background-color: rgba(0, 135, 245, 0.6);}
  100% {background-color: rgba(0, 135, 245, 0.9);}
}
@keyframes start-flicker {
  0% {background-color: rgba(0, 135, 245, 0.9);}
  50% {background-color: rgba(0, 135, 245, 0.6);}
  100% {background-color: rgba(0, 135, 245, 0.9);}
}
@-webkit-keyframes fade-in {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
@keyframes fade-in {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
@-webkit-keyframes fade-out {
  0% {opacity: 1;}
  100% {opacity: 0;}
}
@keyframes fade-out {
  0% {opacity: 1;}
  100% {opacity: 0;}
}
@-webkit-keyframes going-up {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  100% {-webkit-transform: translateY(-10px);transform: translateY(-10px);}
}
@keyframes going-up {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  100% {-webkit-transform: translateY(-10px);transform: translateY(-10px);}
}
@-webkit-keyframes going-down {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  100% {-webkit-transform: translateY(10px);transform: translateY(10px);}
}
@keyframes going-down {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  100% {-webkit-transform: translateY(10px);transform: translateY(10px);}
}
@-webkit-keyframes going-left {
  0% {-webkit-transform: translateX(0);transform: translateX(0);}
  100% {-webkit-transform: translateX(-10px);transform: translateX(-10px);}
}
@keyframes going-left {
  0% {-webkit-transform: translateX(0);transform: translateX(0);}
  100% {-webkit-transform: translateX(-10px);transform: translateX(-10px);}
}
@-webkit-keyframes working {
  0% {width: 0%;}
  50% {width: 40%;}
  100% {width: 100%;}
}
@keyframes working {
  0% {width: 0%;}
  50% {width: 40%;}
  100% {width: 100%;}
}

